export const reductId = (x) => x?.match(/^.{6}|.{4}$/g)?.join('...');
export const setClipboard = (text) => {
    navigator.clipboard.writeText(text);
};
export const getQueryObject = () => {
    const search = location.search.substring(1);
    return JSON.parse('{"' +
        decodeURI(search)
            .replace(/"/g, '\\"')
            .replace(/&/g, '","')
            .replace(/=/g, '":"') +
        '"}');
};
