const TonWeb = window.TonWeb;
export class Channel {
    channelId;
    addressA;
    addressB;
    initBalanceA;
    initBalanceB;
    isA;
    myKeyPair;
    hisPublicKey;
    apiKey;
    providerUrl;
    ton;
    channel;
    fromWallet;
    wallet;
    constructor(channelId, // Channel ID, for each new channel there must be a new ID
    addressA, // A's funds will be withdrawn to this wallet address after the channel is closed
    addressB, // B's funds will be withdrawn to this wallet address after the channel is closed
    initBalanceA, initBalanceB, isA, myKeyPair, hisPublicKey, apiKey, providerUrl) {
        this.channelId = channelId;
        this.addressA = addressA;
        this.addressB = addressB;
        this.initBalanceA = initBalanceA;
        this.initBalanceB = initBalanceB;
        this.isA = isA;
        this.myKeyPair = myKeyPair;
        this.hisPublicKey = hisPublicKey;
        this.apiKey = apiKey;
        this.providerUrl = providerUrl;
        this.channelId = channelId;
        this.addressA = addressA;
        this.addressB = addressB;
        this.initBalanceA = initBalanceA;
        this.initBalanceB = initBalanceB;
        this.isA = isA;
        this.myKeyPair = myKeyPair;
        this.hisPublicKey = hisPublicKey;
        this.ton = new TonWeb(new TonWeb.HttpProvider(providerUrl, { apiKey }));
        this.channel = this.ton.payments.createChannel({
            channelId,
            addressA,
            addressB,
            initBalanceA,
            initBalanceB,
            isA,
            myKeyPair,
            hisPublicKey,
        });
        this.wallet = this.ton.wallet.create({
            publicKey: myKeyPair.publicKey,
        });
        this.fromWallet = this.channel.fromWallet({
            wallet: this.wallet,
            secretKey: myKeyPair.secretKey,
        });
    }
}
